<template>
  <div class="personalInfo">
    <div class="navtop">
      <van-nav-bar title="个人信息" left-arrow @click-left="back" />
    </div>

    <!-- 头像 -->
    <div class="avatar">
      <img :src="personalInfoData.avatar">
      <van-uploader :after-read="afterRead" />
    </div>

    <div class="form">
      <!-- 昵称 -->
      <van-field v-model="personalInfoData.nickname" label="昵称" input-align="right"/>
      <!-- id号 -->
      <van-cell title="ID" :value="personalInfoData.uid" />
      <!-- 手机号 -->
      <van-cell title="手机号" :value="personalInfoData.phone" />
      <!-- 密码 -->
      <van-cell title="密码" value="点击修改密码" is-link to="/setpword"/>
      

      <!-- 按钮 -->
      <div class="btn">
        <van-button round block color="linear-gradient(to right,#ff9000 0,#ff5000 100%)" @click="onEdit">保存</van-button>
        <van-button round block @click="logOut">退出登录</van-button>
      </div>

    </div>
  </div>
</template>

<script>
import "../assets/style/PersonalInfo.less"
export default {
  name:"PersonalInfo",
  data(){
    return {
      // 个人信息
      personalInfoData:[]
    }
  },
  created(){
    this.getPersonalInfo()
  },
  methods:{
    // 返回
    back(){
      this.$router.back()
    },

    // 修改头像
    afterRead(file) {
      let data = new FormData()
      data.append("file",file.file)

      //获取token
      let tokenString = this.$cookies.get("tokenString");

      //判断token存不存在
      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        method:"POST",
        url:this.baseUrl +"/upload/image",
        data,
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      }).then(res=>{
        console.log(res);
        this.$toast.clear();

        if (res.data.status == 200) {
          this.$toast({
            message: res.data.msg,
            forbidClick: true,
            duration: 1000,
          });

          // 传头像路径并修改
          this.getPersonalInfo(res.data.data.url)

          

        }else {
          this.$toast({
            message: res.data.msg,
            forbidClick: true,
            duration: 3000,
          });
        }
      }).catch((err) => {
        this.$toast.clear();
        console.log("err ==> ", err);
      })
    },

    // 获取个人信息
    getPersonalInfo(imgUrl){
      //获取token
      let tokenString = this.$cookies.get("tokenString");

      //判断token存不存在
      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method:"GET",
        url:this.baseUrl+"/user",
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      }).then((res) => {
          // console.log("res getAddressListData ==> ", res);
          this.$toast.clear();

          if (imgUrl) {
            res.data.data.switchUserInfo[0].avatar = imgUrl
          }
          
          if (res.data.status == 200) {

            this.personalInfoData = res.data.data.switchUserInfo[0]

          }else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            this.$router.push("/login")
          }
      }).catch((err) => {
        this.$toast.clear();
        console.log("err ==> ", err);
      });
    },

    //保存修改的信息
    onEdit(){
      //获取token
      let tokenString = this.$cookies.get("tokenString");

      //判断token存不存在
      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method:"POST",
        url:this.baseUrl+"/user/edit",
        data:{
          avatar: this.personalInfoData.avatar,
          nickname: this.personalInfoData.nickname
        },
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      }).then((res) => {
          console.log("res getAddressListData ==> ", res);
          this.$toast.clear();
          if (res.data.status == 200) {
            this.$router.push("/user")
          }
      }).catch((err) => {
        this.$toast.clear();
        console.log("err ==> ", err);
      });
    },

    // 退出登录
    logOut(){

      //获取token
      let tokenString = this.$cookies.get("tokenString");

      //判断token存不存在
      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      // 提示确定要退出吗
      this.$dialog.confirm({
          title: '提示',
          message: '确定退出登录吗？',
        }).then(() => {

          this.axios({
            method:"GET",
            url:this.baseUrl + "/logout",
            headers: {
              "Authori-zation": "Bearer " + tokenString,
            },
          }).then(()=>{
            this.$router.push("/")
          })

        }).catch(()=>{

        })

    }

  }
}
</script>